.pagination {
  border: 1px solid #dddddd;
  display: table;
  margin: 30px auto; }
  .pagination_item {
    display: table-cell;
    -webkit-transition-duration: 0.3s;
         -o-transition-duration: 0.3s;
            transition-duration: 0.3s; }
    .pagination_item:first-child a {
      border-left: 0; }
  .pagination a {
    width: 60px;
    height: 60px;
    display: block;
    -webkit-transition-duration: 0.3s;
         -o-transition-duration: 0.3s;
            transition-duration: 0.3s;
    border-left: 2px solid #dddddd;
    text-align: center;
    line-height: 60px;
    color: blue; }
    .pagination a:hover {
      background: #eeeeee; }
    .pagination a:active {
      background: #dddddd; }

.active a {
  pointer-events: none !important;
  color: #999999; }



